import Vue from 'vue'
import App from './App'
import router from './router'
import Axios from 'axios'

Vue.prototype.$http = Axios

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})

Vue.filter('formatDate', function (str) {
  if (!str) return ''
  var date = new Date(str)
  var time = new Date().getTime() - date.getTime()
  if (time < 0) {
    return ''
  } else if (time / 1000 < 30) {
    return 'ÂàöÂàö'
  } else if (time / 1000 < 60) {
    return parseInt((time / 1000)) + 'ÁßíÂâç'
  } else if (time / 60000 < 60) {
    return parseInt((time / 60000)) + 'ÂàÜÈíüÂâç'
  } else if (time / 3600000 < 24) {
    return parseInt((time / 3600000)) + 'Â∞èÊó∂Ââç'
  } else if (time / 86400000 < 31) {
    return parseInt((time / 86400000)) + 'Â§©Ââç'
  } else if (time / 2592000000 < 31) {
    return parseInt((time / 2592000000)) + 'ÊúàÂâç'
  } else {
    return parseInt((time / 31536000000)) + 'Âπ¥Ââç'
  }
})

Vue.filter('tabFormatter', function (post) {
  if (post.good == true) {
    return 'Á≤æÂçé'
  } else if (post.top == true) {
    return 'ÁΩÆÈ°∂'
  } else if (post.tab == 'ask') {
    return 'ÈóÆÁ≠î'
  } else if (post.tab == 'share') {
    return 'ÂàÜ‰∫´'
  } else {
    return 'ÊãõËÅò'
  }
})


