<template>
  <div class="postList">
    <!--Âú®Êï∞ÊçÆÊú™ËøîÂõûÁöÑÊó∂ÂÄôÔºåÊòæÁ§∫Ëøô‰∏™Ê≠£Âú®Âä†ËΩΩÁöÑgif-->
    <div class="loading" v-if="isLoading">
      <img src="../assets/loading.gif" />
    </div>
    <!--‰ª£Ë°®ÊàëÈñÄÁöÑ‰∏ªÈ¢òÂ∏ñÂ≠êÂàóË°®-->
    <div class="posts" v-else>
      <ul>
        <li>
          <div class="toobar">
            <span>ÂÖ®ÈÉ®</span>
            <span>Á≤æÂçé</span>
            <span>ÂàÜ‰∫´</span>
            <span>ÈóÆÁ≠î</span>
            <span>ÊãõËÅò</span>
            <span>ÂÆ¢Êà∑Á´ØÊµãËØï</span>
          </div>
        </li>
        <li v-for="post in posts">
          <!--Â§¥ÂÉè-->
          <img :src="post.author.avatar_url" alt />
          <!--ÂõûÂ§ç/ÊµèËßà-->
          <span class="allcount">
            <span class="reply_count">{{post.reply_count}}</span>/
            <span class="visit_count">{{post.visit_count}}</span>
          </span>
          <!--Â∏ñÂ≠êÁöÑÂàÜÁ±ª-->
          <span
            :class="[{put_good:(post.good  == true),put_top:(post.top  == true),
        'topiclist-tab':(post.good  != true && post.top  != true)}]"
          >
            <span>{{post | tabFormatter}}</span>
          </span>
          <!--Ê†áÈ¢ò-->
          <router-link
            :to="{
          name:'post_content',
          params:{
            id:post.id,
            name:post.author.loginname
          }
          }"
          >
            <span>{{post.title}}</span>
          </router-link>
          <!--ÊúÄÁµÇÂõûÂ§çÊó∂Èó¥-->
          <span class="last_reply">{{post.last_reply_at | formatDate}}</span>
        </li>
        <li>
          <!--ÂàÜÈ°µ-->
          <pagination @handleList="renderList"></pagination>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import pagination from "./Pagination";
export default {
  name: "PostList",
  data() {
    return {
      isLoading: false,
      posts: [], //‰ª£Ë°®È°µÈù¢ÁöÑÂàóË°®Êï∞ÁªÑ
      postpage: 1
    };
  },
  components: {
    pagination
  },
  methods: {
    getData() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.postpage,
            limit: 20
          }
        })
        .then(res => {
          this.isLoading = false; //Âä†ËΩΩÊàêÂäüÔºåÂéªÈô§Âä®Áîª
          this.posts = res.data.data;
        })
        .catch(function(err) {
          //Â§ÑÁêÜËøîÂõûÂ§±Ë¥•ÂêéÁöÑÈóÆÈ¢ò
          console.log(err);
        });
    },
    renderList(value) {
      this.postpage = value;
      this.getData();
    }
  },
  beforeMount() {
    this.isLoading = true; //Âä†ËΩΩÊàêÂäü‰πãÂâçÊòæÁ§∫Âä†ËΩΩÂä®Áîª
    this.getData(); //Âú®È°µÈù¢Âä†ËΩΩ‰πãÂâçËé∑ÂèñÊï∞ÊçÆ
  }
};
</script>

<style scoped>
.postList {
  background-color: #e1e1e1;
}
.posts {
  color: #333;
  margin: 0 auto;
  max-width: 1400px;
}
.postList img {
  height: 30px;
  width: 30px;
  vertical-align: middle;
}
ul {
  list-style: none;
  width: 100%;
  max-width: 1400px;
  margin: 0 auto;
  margin-top: 10px;
  padding-inline-start: 0px;
}
ul li:not(:first-child) {
  padding: 9px;
  font-size: 15px;
  font-family: "Helvetica Neue", "Luxi Sans", "DejaVu Sans", Tahoma,
    "Hiragino Sans GB", STHeiti, sans-serif !important;
  font-weight: 400;
  background-color: white;
  border-top: 1px solid #f0f0f0;
}
li:not(:first-child):hover {
  background: #f5f5f5;
}
li:last-child:hover {
  background: white;
}
li span {
  line-height: 30px;
}
.allcount {
  width: 70px;
  display: inline-block;
  text-align: center;
  font-size: 12px;
  color: #333;
}
.visit_count {
  color: #b4b4b4;
}
.reply_count {
  color: #9e78c0;
  font-size: 14px;
}
.put_good,
.put_top {
  background: #80bd01;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  color: #fff;
  font-size: 12px;
  margin-right: 10px;
}

.topiclist-tab {
  background-color: #e5e5e5;
  color: #999;
  padding: 2px 4px;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -o-border-radius: 3px;
  font-size: 12px;
  margin-right: 10px;
}

.last_reply {
  text-align: right;
  min-width: 50px;
  display: inline-block;
  white-space: nowrap;
  float: right;
  color: #778087;
  font-size: 12px;
}

.toobar {
  height: 40px;
  background-color: #f5f5f5;
  padding: 0 10px;
  border-radius: 3px 3px 0 0;
}

.toobar span {
  font-size: 14px;
  color: #80bd01;
  line-height: 40px;
  margin: 0 10px;
  cursor: pointer;
}

.toobar span:hover {
  color: #9e78c0;
}

a {
  text-decoration: none;
  color: black;
}

a:hover {
  text-decoration: underline;
}
.loading {
  text-align: center;
  padding-top: 300px;
}
</style>



